html,
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* color: #636262; */
}

/* button {
  background-color: #00b96b !important;
} */

input,
textarea,
select {
  min-height: 45px;
  width: 100%;
  border: 1px solid #c6c6c6;
  padding-left: 15px !important;
  border-radius: 2px;
}

.checkbox-reset{
  min-height: unset !important;
  padding-left: unset !important;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  border: 1px solid #353535;
}

textarea {
  margin-top: 10px !important;
  min-height: 100px;
}

button {
  border: none;
  outline: none;
  cursor: pointer;
  height: 45px !important;
  box-shadow: none !important;
  border-radius: 2px !important;
}

.ant-table button {
  height: 17px !important;
  border-radius: 2px !important;
}

/* label {
  margin: 0px !important;
  padding: 0px !important;
  color: #353535 !important;
} */

a {
  text-decoration: none;
  color: var(--primary) !important;
}

.ant-radio-input {
  accent-color: #232323 !important;
}

i {
  font-size: 22px;
  cursor: pointer;
}

table {
  border: 1px solid #c7c7c7 !important;
  border-radius: 5px;
}

.ant-btn {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
}

.ant-form-item {
  margin-bottom: 0px !important;
}

.ant-form-item-label {
  padding: 0px !important;
}
.table-fixed{
  height: 65vh;
}

.layout-parent {
  padding: 10px;
  gap: 10px;
  height: 92vh;
  width: 100%;
  overflow: scroll;
}

.body {
  width: 100%;
  padding: 0 10px;
  border: 1px solid #c6c6c6;
  border-radius: 4px;
  overflow-y: scroll;
  padding: 20px;
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  background-color: var(--primary);
  border-radius: 4px;
  padding: 5px 20px;
  max-width: 250px;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  margin-top: 15px;
}

.logo h1 {
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.logo i {
  font-size: 20px;
  color: white;
}

.menu-items {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.menu-item {
  color: white;
  display: flex;
  gap: 10px;
  align-items: center;

  cursor: pointer;
  padding: 7px 5px;
}

.menu-item i {
  font-size: 18px !important;
}

.menu-item span {
  font-size: 14px;
}

.active-menu-item {
  /* border: 1px solid white; */
  background-color: white !important;
  color: var(--primary) !important;
  border-radius: 4px;
}

.user-info {
  display: flex;
  align-items: end;
  gap: 10px;
  padding: 10px;
  color: white !important;
  font-size: 14px;
  background-color: rgb(55, 70, 120);
  border-radius: 4px;
  margin-bottom: 20px;
}

.user-info i {
  font-size: 18px;
}


/* styles.css */
.custom-sub-menu-items {
/* Add your custom styles for the sub-menu items */
/* For example: */
background-color: #f4f4f4;
padding: 12px;
border-radius: 4px;
}

.custom-sub-menu-item {
/* Add your custom styles for the individual sub-menu items */
/* For example: */
display: flex;
align-items: center;
padding: 8px 12px;
cursor: pointer;
transition: background-color 0.3s ease;
}

.custom-sub-menu-item:hover {
/* Add hover styles if needed */
background-color: #e4e4e4;
}


.loader-parent {
  position: fixed;
  inset: 0px;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999 !important;
}

.loader {
  height: 60px;
  width: 60px;
  border: 5px solid #FFF;
  border-top-color: var(--primary);
  border-radius: 50%;
  animation: spin 1s infinite linear;
}

@keyframes spin {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}

